<template>
  <div class="order_container">
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-card>
      <!--  -->
      <el-row>
        <el-col :span="6">
          <el-input placeholder="">
            <el-button slot="append" icon="el-icon-search"></el-button>
          </el-input>
        </el-col>
      </el-row>
      <!--  -->
      <el-table :data="ordersList" stripe style="width: 100%" border>
        <el-table-column label="#" type="index" width="90"> </el-table-column>
        <el-table-column label="" prop="order_number" width="500"></el-table-column>
        <el-table-column label="" prop="order_price" width="180"></el-table-column>
        <el-table-column label="" prop="order_pay" width="180">
          <template slot-scope="scope">
            <el-tag v-if="scope.row.order_pay === 1" type="success"></el-tag>
            <el-tag v-else type="danger"></el-tag>
          </template>
        </el-table-column>
        <el-table-column label="" prop="is_send" width="180"></el-table-column>
        <el-table-column label="" prop="create_time" width="280">
          <template slot-scope="scope">
            {{ scope.row.create_time | dateFormate }}
          </template>
        </el-table-column>
        <el-table-column label="">
          <template slot-scope="scope">
            <!--  -->
            <el-button icon="el-icon-edit" type="primary" @click="showEdit(scope.row)"></el-button>
            <el-button icon="el-icon-location-outline" type="success" @click="showLogistic(scope.row)"></el-button>
          </template>
        </el-table-column>
      </el-table>
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[5, 10, 20, 30]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      >
      </el-pagination>
    </el-card>
    <!--  -->
    <el-dialog title="" :visible.sync="editDialogVisible" width="30%">
      <el-form :model="addressForm" :rules="addressFormRules" ref="addressFormRef" label-width="100px">
        <el-form-item label="" prop="address1">
          <el-cascader
            class="cc"
            v-model="addressForm.address1"
            :options="cityData"
            @change="handleChange"
          ></el-cascader>
        </el-form-item>
        <el-form-item label="" prop="address2">
          <el-input v-model="addressForm.address2"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editDialogVisible = false"> </el-button>
        <el-button type="primary" @click="eidtOrder"> </el-button>
      </span>
    </el-dialog>
    <!--  -->
    <el-dialog title="" :visible.sync="logisticsVisible">
      <el-timeline>
        <el-timeline-item v-for="(activity, index) in transportMessage" :key="index" :timestamp="activity.time">
          {{ activity.context }}
        </el-timeline-item>
      </el-timeline>
    </el-dialog>
  </div>
</template>
<script>
import citydata from './citydata'
export default {
  data() {
    return {
      queryInfo: {
        query: '',
        pagenum: 1,
        pagesize: 10,
        // id
        user_id: '',
        // 
        pay_status: ''
      },
      //  
      ordersList: [],
      //   
      total: 0,
      //   
      editDialogVisible: false,
      //   
      addressForm: {
        address1: '',
        address2: ''
      },
      //    
      cityData: citydata,
      addressFormRules: {
        address1: [{ required: true, message: '', trigger: 'blur' }],
        address2: [{ required: true, message: '', trigger: 'blur' }]
      },
      //   
      editForm: {},
      //   
      logisticsVisible: false,
      // 
      transportMessage: []
    }
  },
  methods: {
    async getOrders() {
      const { data: res } = await this.$http.get('orders', { params: this.queryInfo })
      //   console.log(res.data)
      if (res.meta.status !== 200) return this.$message.error('')
      this.ordersList = res.data.goods
      this.total = res.data.total
      //   console.log(this.ordersList)
    },
    // 
    handleSizeChange(size) {
      this.queryInfo.pagesize = size
      this.getOrders()
    },
    handleCurrentChange(newPage) {
      this.queryInfo.pagenum = newPage
      this.getOrders()
    },
    // 
    showEdit(row) {
      this.editForm = row
      this.editDialogVisible = true
    },
    // 
    handleChange() {
      console.log(this.addressForm.address1)
    },
    // 
    eidtOrder() {
      //   console.log(this.addressForm)
      //   console.log(this.editForm)
      this.editForm.consignee_addr = this.addressForm.address1.join('') + ' ' + this.addressForm.address2
      this.editForm.update_time = new Date().getTime()
      //  
      // this.$http.post()
    },
    async showLogistic(row) {
      this.logisticsVisible = true
      console.log(row)
      // 
      const { data: res } = await this.$http.get('/kuaidi/4313006474753')
      if (res.meta.status !== 200) return this.$message.error('')
      this.transportMessage = res.data
    }
  },
  created() {
    this.getOrders()
  }
}
</script>
<style lang="less" scoped>
.cc {
  width: 100%;
}
</style>
