<template>
  <div class="rights_container">
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-card>
      <el-table :data="rightsList" border>
        <el-table-column type="index" label="#"></el-table-column>
        <el-table-column prop="authName" label=""></el-table-column>
        <el-table-column prop="path" label=""></el-table-column>
        <el-table-column prop="level" label="">
          <template slot-scope="scope">
            <!-- {{ scope.row }} -->
            <el-tag v-if="scope.row.level === '0'"></el-tag>
            <el-tag type="success" v-if="scope.row.level === '1'"></el-tag>
            <el-tag type="warning" v-if="scope.row.level === '2'"></el-tag>
          </template>
        </el-table-column>
      </el-table>
    </el-card>
  </div>
</template>
<script>
export default {
  created() {
    this.getRightsList()
  },
  data() {
    return {
      rightsList: []
    }
  },
  methods: {
    async getRightsList() {
      const { data: res } = await this.$http.get('rights/list')
      //   console.log(res)
      if (res.meta.status !== 200) return this.$message.error('')
      this.$message.success('')
      this.rightsList = res.data
    }
  }
}
</script>
<style lang="less" scoped></style>
