<template>
  <div class="container_cate">
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-card>
      <!--  -->
      <el-row>
        <el-col><el-button type="primary" @click="showAddCategory"></el-button></el-col>
      </el-row>
      <!-- table-tree  -->
      <tree-table
        :data="categoeyList"
        :columns="columns"
        :selection-type="false"
        :expand-type="false"
        show-index
        index-text="#"
        border
        :show-row-hover="false"
      >
        <!--  -->
        <template slot="isOk" slot-scope="scope">
          <i class="el-icon-success" v-if="scope.row.cat_deleted === false" style="color:lightgreen"></i>
          <i class="el-icon-error" v-else style="color:lightgreen"></i>
        </template>
        <!--  -->
        <template slot-scope="scope" slot="order">
          <el-tag v-if="scope.row.cat_level === 0"></el-tag>
          <el-tag type="success" v-if="scope.row.cat_level === 1"></el-tag>
          <el-tag type="warning" v-if="scope.row.cat_level === 2"></el-tag>
        </template>
        <!--  -->
        <template slot-scope="scope" slot="operate">
          <el-button type="primary" icon="el-icon-edit" @click="toEidtCate(scope.row)"></el-button>
          <el-button type="danger" icon="el-icon-delete" @click="deleteByCateId(scope.row)"></el-button>
        </template>
      </tree-table>
      <!--  -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="params.pagenum"
        :page-sizes="[1, 3, 5, 10]"
        :page-size="params.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      >
      </el-pagination>
    </el-card>
    <!--  -->
    <el-dialog title="" :visible.sync="dialogFormVisible" width="30%" @close="clearBox">
      <el-form :model="addForm" ref="addFormRef" :rules="addFormRules" label-width="80px">
        <el-form-item label="" prop="cat_name">
          <el-input v-model="addForm.cat_name"></el-input>
        </el-form-item>
        <el-form-item label="" prop="cat_pid">
          <!-- options  -->
          <!-- props   -->
          <!-- v-model  data /id -->
          <!-- change   clearable -->
          <!-- change-on-selectfalse -->
          <el-cascader
            :options="allParenCate"
            :props="cascaderProps"
            v-model="selectedKeys"
            @change="parentCateChange"
            clearable
            change-on-select
          ></el-cascader>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="clearBox"> </el-button>
        <el-button type="primary" @click="addCate"> </el-button>
      </div>
    </el-dialog>

    <!--  -->
    <el-dialog title="" :visible.sync="editFormVisible" width="30%">
      <el-form :model="editForm" ref="editFormRef" :rules="addFormRules" label-width="80px">
        <el-form-item label="" prop="cat_name">
          <el-input v-model="editForm.cat_name"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="editFormVisible = false"> </el-button>
        <el-button type="primary" @click="editCate"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
export default {
  data() {
    return {
      params: {
        // 
        type: 3,
        pagenum: 1,
        pagesize: 5
      },
      // 
      categoeyList: [],
      //   
      total: 0,
      // tabletable-tree
      columns: [
        {
          label: '',
          prop: 'cat_name'
        },
        {
          label: '',
          // 
          type: 'template',
          // 
          template: 'isOk'
        },
        {
          label: '',
          type: 'template',
          template: 'order'
        },
        {
          label: '',
          type: 'template',
          template: 'operate'
        }
      ],
      // 
      addForm: {
        cat_name: '',
        // id
        cat_pid: 0,
        //  0
        cat_level: 0
      },
      //  
      addFormRules: {
        cat_name: [{ required: true, message: '', trigger: blur }]
      },
      // 
      allParenCate: [],
      // 
      cascaderProps: {
        // 
        value: 'cat_id',
        // 
        label: 'cat_name',
        // 
        children: 'children'
      },
      // 
      dialogFormVisible: false,
      // id
      selectedKeys: [],
      // 
      editForm: {},
      editFormVisible: false
    }
  },
  created() {
    this.getAllCate()
  },
  methods: {
    // 
    async getAllCate() {
      const { data: res } = await this.$http.get('categories', { params: this.params })
      if (res.meta.status !== 200) return this.$message.error('')
      // console.log(res.data.result)
      this.categoeyList = res.data.result
      this.total = res.data.total
    },
    // 
    handleSizeChange(newSize) {
      this.params.pagesize = newSize
      this.getAllCate()
    },
    // 
    handleCurrentChange(currentPage) {
      this.params.pagenum = currentPage
      this.getAllCate()
    },
    showAddCategory() {
      this.dialogFormVisible = true
      this.getParentCate()
    },
    // 
    async getParentCate() {
      const { data: res } = await this.$http.get('categories', { params: { type: 2 } })
      // console.log(res.data)
      if (res.meta.status !== 200) return this.$message.error('')
      this.allParenCate = res.data
    },
    // 
    parentCateChange() {
      //  id
      // console.log(this.selectedKeys)
      // selectedKeyslength>0   
      //   
      const keysLength = this.selectedKeys.length
      // addForm
      if (keysLength > 0) {
        // id  id
        this.addForm.cat_pid = this.selectedKeys[keysLength - 1]
        // 
        this.addForm.cat_level = keysLength
        //  
      } else {
        this.addForm.cat_pid = 0
        // 
        this.addForm.cat_level = 0
      }
    },
    //   
    clearBox() {
      this.dialogFormVisible = false
      // 
      this.$refs.addFormRef.resetFields()
      this.selectedKeys = []
      // addForm
      this.addForm.cat_pid = 0
      this.addForm.cat_level = 0
    },
    addCate() {
      console.log(this.addForm)
      this.$refs.addFormRef.validate(async (valid) => {
        if (!valid) return this.$message.error('')
        // ajax 
        const { data: res } = await this.$http.post('categories', this.addForm)
        console.log(res)
        if (res.meta.status !== 201) return this.$message.error('')
        this.$message.success('')
        this.getAllCate()
        this.dialogFormVisible = false
      })
    },
    // 
    async deleteByCateId(role) {
      const { data: res } = await this.$http.delete(`categories/${role.cat_id}`, { id: role.cat_pid })
      if (res.meta.status !== 200) return this.$message.success('')
      this.$message.success('')
      this.getAllCate()
    },
    // id
    async toEidtCate(row) {
      this.editFormVisible = true
      // id 
      const { data: res } = await this.$http.get(`categories/${row.cat_id}`)
      if (res.meta.status !== 200) return this.$message.error('')
      this.editForm = res.data
    },
    // 
    editCate() {
      this.$refs.editFormRef.validate(async (valid) => {
        if (!valid) return this.$message.error('')
        // console.log(this.editForm)
        const { data: res } = await this.$http.put(`categories/${this.editForm.cat_id}`, {
          cat_name: this.editForm.cat_name
        })
        if (res.meta.status !== 200) return this.$message.error('')
        this.$message.success('')
        this.editFormVisible = false
        this.getAllCate()
      })
    }
  }
}
</script>
<style lang="less" scoped>
.el-row {
  margin-bottom: 20px;
}
.el-cascader {
  width: 100%;
}
</style>
