<template>
  <div class="params_container">
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-card>
      <el-alert type="warning" show-icon title="" :closable="false"> </el-alert>
      <!--  -->
      <el-row>
        <el-col>
          <span></span>
          <!--  -->
          <el-cascader
            v-model="selectedKeys"
            :options="allCategories"
            :props="cateDisplayAttrs"
            @change="handleChange"
            clearable
          ></el-cascader>
        </el-col>
      </el-row>
      <!-- tabs   -->
      <el-tabs v-model="activeName" @tab-click="handleClick">
        <el-tab-pane label="" name="many">
          <el-button :disabled="isBtnDiasabled" type="primary" size="mini" @click="addDialogVisible = true">
            
          </el-button>
          <!--  -->
          <el-table :data="manyTableData" border stripe>
            <!--  -->
            <el-table-column type="expand">
              <!-- tabs -->
              <template slot-scope="scope">
                <el-tag v-for="(item, i) in scope.row.attr_vals" :key="i" closable @close="deleteAttrVal(scope.row, i)">
                  {{ item }}
                </el-tag>
                <!--  -->
                <el-input
                  class="input-new-tag"
                  v-if="scope.row.inputVisible"
                  v-model="scope.row.inputValue"
                  ref="saveTagInput"
                  size="small"
                  @keyup.enter.native="handleInputConfirm(scope.row)"
                  @blur="handleInputConfirm(scope.row)"
                >
                </el-input>
                <el-button v-else class="button-new-tag" size="small" @click="showInput(scope.row)"
                  >+ New Tag</el-button
                >
              </template>
            </el-table-column>
            <!--  -->
            <el-table-column type="index" label="#"></el-table-column>
            <el-table-column label="" prop="attr_name"></el-table-column>
            <el-table-column label="">
              <template slot-scope="scope">
                <el-button type="primary" icon="el-icon-edit" @click="toEditParams(scope.row)"> </el-button>
                <el-button type="danger" icon="el-icon-delete" @click="deleteParamsById(scope.row)"> </el-button>
              </template>
            </el-table-column>
          </el-table>
        </el-tab-pane>
        <el-tab-pane label="" name="only">
          <el-button :disabled="isBtnDiasabled" type="primary" size="mini" @click="addDialogVisible = true"
            ></el-button
          >
          <!--  -->
          <el-table :data="onlyTableData" border stripe>
            <!--  -->
            <el-table-column type="expand">
              <template slot-scope="scope">
                <el-tag v-for="(item, i) in scope.row.attr_vals" :key="i" closable @close="deleteAttrVal(scope.row, i)">
                  {{ item }}
                </el-tag>
                <el-input
                  class="input-new-tag"
                  v-if="scope.row.inputVisible"
                  v-model="scope.row.inputValue"
                  ref="saveTagInput"
                  size="small"
                  @keyup.enter.native="handleInputConfirm(scope.row)"
                  @blur="handleInputConfirm(scope.row)"
                >
                </el-input>
                <el-button v-else class="button-new-tag" size="small" @click="showInput(scope.row)"
                  >+ New Tag</el-button
                >
              </template>
            </el-table-column>
            <!--  -->
            <el-table-column type="index" label="#"></el-table-column>
            <el-table-column label="" prop="attr_name"></el-table-column>
            <el-table-column label="">
              <template slot-scope="scope">
                <el-button type="primary" icon="el-icon-edit" @click="toEditParams(scope.row)"> </el-button>
                <el-button type="danger" icon="el-icon-delete" @click="deleteParamsById(scope.row)"> </el-button>
              </template>
            </el-table-column>
          </el-table>
        </el-tab-pane>
      </el-tabs>
    </el-card>
    <!--  -->
    <el-dialog :title="'' + titleText" :visible.sync="addDialogVisible" width="30%" @close="clearAddForm">
      <el-form :model="addForm" :rules="addRules" ref="addFormRef" label-width="100px" class="demo-ruleForm">
        <el-form-item :label="titleText" prop="name">
          <el-input v-model="addForm.attr_name"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="addDialogVisible = false"> </el-button>
        <el-button type="primary" @click="addParams()"> </el-button>
      </span>
    </el-dialog>
    <!--  -->
    <el-dialog :title="'' + titleText" :visible.sync="editDialogVisible" width="30%">
      <el-form :model="editForm" :rules="addRules" ref="editFormRef" label-width="100px" class="demo-ruleForm">
        <el-form-item :label="titleText" prop="name">
          <el-input v-model="editForm.attr_name"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="addDialogVisible = false"> </el-button>
        <el-button type="primary" @click="editParams"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
export default {
  data() {
    return {
      // 
      allCategories: [],
      // ()
      cateDisplayAttrs: {
        value: 'cat_id',
        label: 'cat_name',
        children: 'children'
      },
      //  id
      selectedKeys: [],
      // tabs 
      activeName: 'many',
      // 
      manyTableData: [],
      // 
      onlyTableData: [],
      // 
      addDialogVisible: false,
      // 
      addForm: {
        attr_name: ''
      },
      addRules: {
        name: [
          { required: true, message: '', trigger: blur },
          { min: 2, message: '2' }
        ]
      },
      // 
      editForm: {},
      // 
      editDialogVisible: false
    }
  },
  created() {
    this.getAllCates()
  },
  methods: {
    async getAllCates() {
      //  
      const { data: res } = await this.$http.get('categories')
      if (res.meta.status !== 200) return this.$message.error('')
      this.allCategories = res.data
    },
    //  
    async handleChange() {
      this.getParamsData()
    },
    // tab 
    handleClick() {
      // console.log(this.activeName)
      this.getParamsData()
    },
    // 
    async getParamsData() {
      const length = this.selectedKeys.length
      //  
      if (length !== 3) {
        this.selectedKeys = []
        // many only
        this.manyTableData = []
        this.onlyTableData = []
        this.$message.error('')
        return ''
      }
      const { data: res } = await this.$http.get(`categories/${this.getCateId}/attributes`, {
        params: { sel: this.activeName }
      })
      //   console.log(res.data)
      // attr_vals  
      res.data.forEach((item) => {
        item.attr_vals = item.attr_vals ? item.attr_vals.split(' ') : []
        //  
        item.inputVisible = false
        //  
        item.inputValue = ''
      })
      //   console.log(res.data)
      //   attr_val

      if (res.meta.status !== 200) return this.$message.error('')
      if (this.activeName === 'only') {
        this.onlyTableData = res.data
      } else {
        this.manyTableData = res.data
      }
    },
    async addParams() {
      this.addForm.cat_id = this.getCateId
      this.addForm.attr_sel = this.activeName
      //   console.log(this.addForm)
      //   
      const { data: res } = await this.$http.post(`categories/${this.addForm.cat_id}/attributes`, this.addForm)
      if (res.meta.status !== 201) return this.$message.error('')
      this.$message.success('')
      this.addDialogVisible = false
      this.getParamsData()
    },
    clearAddForm() {
      this.addForm.attr_name = ''
      this.$refs.addFormRef.resetFields()
    },
    // 
    async deleteParamsById(row) {
      console.log(row)
      const { data: res } = await this.$http.delete(`categories/${row.cat_id}/attributes/${row.attr_id}`)
      if (res.meta.status !== 200) return this.$message.error('')
      this.getParamsData()
    },
    // 
    toEditParams(row) {
      //   console.log(row)
      this.editDialogVisible = true
      this.editForm = row
    },
    // 
    async editParams() {
      //  console.log(this.editForm)
      this.saveAttrVal()
    },
    //   
    async saveAttrVal() {
      console.log(this.editForm)
      const { data: res } = await this.$http.put(
        `categories/${this.editForm.cat_id}/attributes/${this.editForm.attr_id}`,
        {
          attr_name: this.editForm.attr_name,
          attr_sel: this.editForm.attr_sel,
          attr_vals: this.editForm.attr_vals.join(' ')
        }
      )
      if (res.meta.status !== 200) return this.$message.error('')
      this.editDialogVisible = false
      this.$message.success('')
      this.getParamsData()
    },
    // a attr_val
    deleteAttrVal(row, index) {
      this.editForm = row
      console.log(this.editForm)
      this.editForm.attr_vals.splice(index, 1)
      this.saveAttrVal()
    },
    //  
    handleInputConfirm(row) {
      row.inputVisible = false
      this.editForm = row
      if (this.editForm.inputValue.trim().length === 0) return this.$message.error('0')
      this.editForm.attr_vals.push(row.inputValue)
      console.log(this.editForm)
      this.saveAttrVal()
    },
    showInput(row) {
      console.log(row)
      row.inputVisible = true
      row.inputValue = ''
      // 
      this.$nextTick((_) => {
        this.$refs.saveTagInput.$refs.input.focus()
      })
    }
  },
  computed: {
    //  
    isBtnDiasabled() {
      // 3 
      return this.selectedKeys.length !== 3
    },
    // id 
    getCateId() {
      return this.selectedKeys.length === 3 ? this.selectedKeys[2] : null
    },
    titleText() {
      return this.activeName === 'many' ? '' : ''
    }
  }
}
</script>
<style lang="less" scoped>
.el-cascader {
  width: 30%;
}
.el-tag {
  margin: 15px;
}
.input-new-tag {
  width: 10%;
}
</style>
