<template>
  <div class="list_container">
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-card>
      <!--  -->
      <el-row :gutter="20">
        <el-col :span="8">
          <el-input placeholder="" v-model="queryInfo.query" clearable @change="getGoodsList">
            <el-button slot="append" icon="el-icon-search" @click="getGoodsList"></el-button>
          </el-input>
        </el-col>
        <el-col :span="4">
          <el-button type="primary" @click="goAddPage"></el-button>
        </el-col>
      </el-row>
    </el-card>
    <!-- table  -->
    <el-table :data="allGoodsList" stripe border style="width: 100%">
      <el-table-column type="index" label="#" width="80"> </el-table-column>
      <el-table-column prop="goods_name" label="" width="900"> </el-table-column>
      <el-table-column prop="goods_price" label="" width="120"> </el-table-column>
      <el-table-column prop="goods_weight" label="" width="100"> </el-table-column>
      <el-table-column prop="add_time" label="">
        <template slot-scope="scope">
          {{ scope.row.add_time | dateFormate }}
        </template>
      </el-table-column>
      <el-table-column label="">
        <template slot-scope="scope">
          <el-button type="primary" icon="el-icon-edit"></el-button>
          <el-button type="danger" icon="el-icon-delete" @click="deleteGoodsById(scope.row)"> </el-button>
        </template>
      </el-table-column>
    </el-table>
    <!--  -->
    <el-pagination
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="queryInfo.pagenum"
      :page-sizes="[5, 10, 20, 50]"
      :page-size="queryInfo.pagesize"
      layout="total, sizes, prev, pager, next, jumper"
      :total="total"
    >
    </el-pagination>
  </div>
</template>
<script>
export default {
  data() {
    return {
      queryInfo: {
        // 
        query: '',
        pagenum: 1,
        pagesize: 20
      },
      // 
      allGoodsList: [],
      // 
      total: 0
    }
  },
  created() {
    this.getGoodsList()
  },
  methods: {
    async getGoodsList() {
      const { data: res } = await this.$http.get('goods', { params: this.queryInfo })
      if (res.meta.status !== 200) return this.$message.error('')
      this.total = res.data.total
      this.allGoodsList = res.data.goods
      //   console.log(res)
    },
    // -
    handleCurrentChange(newPage) {
      //   console.log(newPage)
      this.queryInfo.pagenum = newPage
      this.getGoodsList()
    },
    handleSizeChange(size) {
      //   console.log(size)
      this.queryInfo.pagesize = size
      this.getGoodsList()
    },
    // 
    async deleteGoodsById(row) {
      const { data: res } = await this.$http.delete(`goods/${row.goods_id}`)
      if (res.meta.status !== 200) return this.$message.error('')
      this.$message.success('')
      this.getGoodsList()
    },
    goAddPage() {
      // 
      this.$router.push('/goods/add')
    }
  }
}
</script>
<style lang="less" scoped></style>
