<template>
  <div class="addGoods_container">
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <el-card>
      <el-alert title="" type="info" center show-icon :closable="false"> </el-alert>
      <!--  -->
      <el-steps :space="200" :active="activeIndex - 0" finish-status="success" align-center>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
      </el-steps>
      <!-- tab -->
      <el-form :model="addGoodsForm" :rules="addGoodsFormRules" ref="addGoodsFormRef">
        <el-tabs v-model="activeIndex" :tab-position="'left'" :before-leave="beforeTabLeave" @tab-click="tabClick">
          <!--  -->
          <el-tab-pane label="" name="0">
            <el-form-item label="" label-position="left" label-width="70" prop="goods_name">
              <el-input v-model="addGoodsForm.goods_name"></el-input>
            </el-form-item>
            <el-form-item label="" prop="goods_price" label-position="left" label-width="70">
              <el-input v-model="addGoodsForm.goods_price" type="number"></el-input>
            </el-form-item>
            <el-form-item label="" prop="goods_weight" label-position="left" label-width="70">
              <el-input v-model="addGoodsForm.goods_weight" type="number"></el-input>
            </el-form-item>
            <el-form-item label="" prop="goods_number" label-position="left" label-width="70">
              <el-input v-model="addGoodsForm.goods_number" type="number"></el-input>
            </el-form-item>
            <el-form-item label="" prop="goods_cat" label-position="left" label-width="70">
              <el-cascader
                v-model="addGoodsForm.goods_cat"
                :options="cateList"
                :props="cateProps"
                @change="handleChange"
              ></el-cascader>
            </el-form-item>
          </el-tab-pane>
          <!--  -->
          <el-tab-pane label="" name="1">
            <el-form-item class="clearMargin" v-for="item in manyTableData" :key="item.attr_id" :label="item.attr_name">
              <!--  -->
              <el-checkbox-group v-model="item.attr_vals">
                <el-checkbox v-for="(item, i) in item.attr_vals1" :key="i" :label="item" border></el-checkbox>
              </el-checkbox-group>
            </el-form-item>
          </el-tab-pane>
          <!--  -->
          <el-tab-pane label="" name="2">
            <el-form>
              <el-form-item
                :label="item.attr_name"
                v-for="item in onlyTableData"
                :key="item.attr_id"
                props="attr_id"
                label-position="left"
                label-width="70"
              >
                <el-input v-model="item.attr_vals" type="text"></el-input>
              </el-form-item>
            </el-form>
          </el-tab-pane>
          <!--  -->
          <el-tab-pane label="" name="3">
            <!-- action url -->
            <el-upload
              :action="actionUrl"
              :on-preview="handlePreview"
              :on-remove="handleRemove"
              :headers="headerobj"
              list-type="picture"
              :on-success="handleSuccess"
            >
              <el-button size="small" type="primary"></el-button>
            </el-upload>
          </el-tab-pane>
          <!--  -->
          <el-tab-pane label=" " name="4">
            <quill-editor id="editor" ref="myQuillEditor" v-model="addGoodsForm.goods_introduce" />
            <el-button type="primary" class="addBtn" @click="addGoods"></el-button>
          </el-tab-pane>
        </el-tabs>
      </el-form>
    </el-card>
    <!--  -->
    <el-dialog title="" :visible.sync="imgVisible" width="30%">
      <img src="previewImgPath" alt="" />
    </el-dialog>
  </div>
</template>
<script>
import { getToken } from '../../utils/token'
export default {
  data() {
    return {
      activeIndex: '4',
      addGoodsForm: {
        goods_name: '',
        goods_price: 10,
        goods_weight: 10,
        goods_number: 10,
        // 
        goods_cat: [1, 3, 6],
        // 
        pics: [],
        // 
        goods_introduce: '',
        //   
        attrs: []
      },
      //   
      cateList: [],
      //   
      addGoodsFormRules: {
        goods_name: [{ required: true, message: '', trigger: 'blur' }],
        goods_price: [
          { required: true, message: '', trigger: 'blur' },
          { min: 0, message: '0', trigger: 'blur' }
        ],
        goods_weight: [
          { required: true, message: '', trigger: 'blur' },
          { min: 0, message: '0', trigger: 'blur' }
        ],
        goods_number: [
          { required: true, message: '', trigger: 'blur' },
          { min: 0, message: '0', trigger: 'blur' }
        ],
        goods_cat: [{ required: true, message: '', trigger: 'blur' }]
      },
      cateProps: {
        label: 'cat_name',
        value: 'cat_id',
        children: 'children'
      },
      //   
      manyTableData: [],
      //  
      onlyTableData: [],
      //   
      actionUrl: 'http://127.0.0.1:8000/api/private/v1/upload',
      //   
      headerobj: {
        Authorization: getToken()
      },
      //   
      previewImgPath: '',
      //   
      imgVisible: false
    }
  },
  mounted() {
    this.displayOnlyGoodsParams()
  },
  methods: {
    async getGoodsCate() {
      const { data: res } = await this.$http.get('categories')
      //   console.log(res)
      if (res.meta.status !== 200) return this.$message.error('')
      this.cateList = res.data
    },
    handleChange() {
      console.log(this.addGoodsForm.goods_cat)
      if (this.addGoodsForm.goods_cat.length !== 3) {
        this.addGoodsForm.goods_cat = []
      }
      console.log(this.addGoodsForm.goods_cat)
    },
    // tab
    beforeTabLeave(newTab, oldTab) {
      //   console.log(':' + oldTab)
      //   console.log('' + newTab)
      //   3
      if (oldTab === '0' && this.addGoodsForm.goods_cat.length !== 3) {
        this.$message.error('')
        return false
      }
    },
    // tab 
    tabClick() {
      // tab
      const index = this.activeIndex
      switch (index) {
        case '1':
          console.log('')
          this.displayManyGoodsParams()
          break
        case '2':
          console.log('')
          this.displayOnlyGoodsParams()
          break
        case '3':
          console.log('')
          break
        case '4':
          console.log('')
          break
      }
    },
    //  
    async displayManyGoodsParams() {
      const { data: res } = await this.$http.get(`categories/${this.cateId}/attributes`, { params: { sel: 'many' } })
      console.log(res)
      if (res.meta.status !== 200) return this.$message.error('')
      res.data.forEach((item) => {
        //    
        item.attr_vals = item.attr_vals.length === 0 ? [] : item.attr_vals.split(' ')
        //  
        item.attr_vals1 = item.attr_vals
      })
      this.manyTableData = res.data
    },
    //  
    async displayOnlyGoodsParams() {
      const { data: res } = await this.$http.get(`categories/${this.cateId}/attributes`, { params: { sel: 'only' } })
      console.log(res)
      if (res.meta.status !== 200) return this.$message.error('')
      this.onlyTableData = res.data
    },
    //  
    handlePreview(file) {
      console.log(file)
      this.previewImgPath = file.response.data.url
      this.imgVisible = true
      console.log(this.previewImgPath)
    },
    //  
    handleSuccess(res) {
      console.log(res)
      if (res.meta.status !== 200) return this.$message.error('')
      //     
      const picPath = { pic: res.data.tmp_path }
      //   
      //   this.imgArr.push(picPath)
      this.addGoodsForm.pics.push(picPath)
      console.log(this.addGoodsForm)
    },
    // 
    handleRemove(file) {
      // 1
      // 2pics
      // 3splicepics
      //   console.log(file)
      const tmPath = file.response.data.tmp_path
      //   console.log(tmPath)
      const index = this.addGoodsForm.pics.findIndex((item) => {
        return item.pic === tmPath
      })
      this.addGoodsForm.pics.splice(index, 1)
    },
    // 
    addGoods() {
      this.$refs.addGoodsFormRef.validate(async (valid) => {
        if (!valid) {
          return this.$message.error('')
        }
        const obj = this.addGoodsForm
        obj.goods_cat = obj.goods_cat.join(',')
        //  attr_vals ,,
        // attr_idattr_name attrs
        this.manyTableData.forEach((item) => {
          const newTab = { attr_id: item.attr_id, attr_vals: item.attr_vals.join(' ') }
          obj.attrs.push(newTab)
        })
        this.onlyTableData.forEach((item) => {
          const newTab = { attr_id: item.attr_id, attr_value: item.attr_vals }
          obj.attrs.push(newTab)
        })
        console.log(obj)
        const { data: res } = await this.$http.post('goods', obj)
        console.log(res)
        if (res.meta.status !== 201) return this.$message.error('')
        this.$message.success('')
        // 
        this.$router.push('/goods')
      })
    }
  },
  created() {
    this.getGoodsCate()
  },
  computed: {
    cateId() {
      if (this.addGoodsForm.goods_cat.length === 3) {
        return this.addGoodsForm.goods_cat[2]
      }
      return null
    }
  }
}
</script>
<style lang="less" scoped>
.el-cascader {
  display: block;
  width: 200px;
}
.clearMargin /deep/ .el-form-item__label {
  margin-left: 0px;
  float: none;
}
.el-checkbox-group {
  margin-left: 0px;
}
.el-checkbox {
  margin: 10px 30px;
}
#editor {
  /deep/.ql-container.ql-snow {
    min-height: 300px;
  }
}
.addBtn {
  margin-top: 15px;
}
</style>
