<template>
  <div class="roles_container">
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-card>
      <el-table :data="roleList" border>
        <el-table-column type="expand">
          <template slot-scope="scope">
            <el-row
              v-for="(item, i) in scope.row.children"
              :key="item.id"
              :class="['bm', i === 0 ? 'bt' : '', 'vcneter']"
            >
              <!--  -->
              <el-col :span="5">
                <el-tag
                  closable
                  :disable-transitions="false"
                  @close="removeRightById(scope.row, item.id)"
                  class="tagMargin"
                  >{{ item.authName }}</el-tag
                >
                <i class="el-icon-caret-right"></i>
              </el-col>
              <!--  -->
              <el-col :span="19">
                <el-row v-for="(item1, i2) in item.children" :key="item1.id" :class="[i2 === 0 ? '' : 'bt', 'vcneter']">
                  <el-col :span="6">
                    <el-tag
                      closable
                      :disable-transitions="false"
                      @close="removeRightById(scope.row, item1.id)"
                      class="tagMargin"
                      type="success"
                      >{{ item1.authName }}</el-tag
                    >
                    <i class="el-icon-caret-right"></i>
                  </el-col>
                  <el-col :span="18">
                    <el-tag
                      closable
                      :disable-transitions="false"
                      @close="removeRightById(scope.row, item2.id)"
                      v-for="item2 in item1.children"
                      :key="item2.id"
                      class="tagMargin tMargin"
                      type="warning"
                      >{{ item2.authName }}
                    </el-tag>
                  </el-col>
                </el-row>
              </el-col>
            </el-row>
          </template>
        </el-table-column>
        <el-table-column label="#" type="index"> </el-table-column>
        <el-table-column label="" prop="roleName"> </el-table-column>
        <el-table-column label="" prop="roleDesc"> </el-table-column>
        <el-table-column label="">
          <template slot-scope="scope">
            <el-button type="primary" icon="el-icon-edit" v-model="scope.row.id"></el-button>
            <el-button type="danger" icon="el-icon-delete"></el-button>
            <el-button type="warning" icon="el-icon-star-off" @click="showRightsDialog(scope.row)"></el-button>
          </template>
        </el-table-column>
      </el-table>
    </el-card>
    <!--  -->
    <el-dialog title="" :visible.sync="setRightsVisible" width="30%" @close="clearDefaultKey">
      <el-tree
        :data="rightsTree"
        :props="rightsProp"
        show-checkbox
        default-expand-all
        node-key="id"
        :default-checked-keys="defaultKey"
        ref="treeRef"
      ></el-tree>
      <span slot="footer" class="dialog-footer">
        <el-button @click="setRightsVisible = false"> </el-button>
        <el-button type="primary" @click="allocateRoles"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
export default {
  created() {
    this.getRoleList()
  },
  data() {
    return {
      roleList: [],
      // 
      setRightsVisible: false,
      // 
      rightsTree: [],
      rightsProp: {
        children: 'children',
        label: 'authName'
      },
      // key
      defaultKey: [],
      roleId: ''
    }
  },
  methods: {
    async getRoleList() {
      const { data: res } = await this.$http.get('roles')
      // console.log(res)
      if (res.meta.status !== 200) return this.$message.error('')
      // this.$message.success('')
      this.roleList = res.data
    },
    async removeRightById(role, rid) {
      const confirmRes = await this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).catch((err) => err)
      if (confirmRes !== 'confirm') {
        return this.$message.info('')
      }
      // ajax
      const { data: res } = await this.$http.delete(`roles/${role.id}/rights/${rid}`)
      console.log(res)
      if (res.meta.status !== 200) return this.$message.error('')
      this.$message.success('')
      //  
      role.children = res.data
    },
    //  role  
    async showRightsDialog(role) {
      //    id
      this.roleId = role.id
      // ajax 
      const { data: res } = await this.$http.get('rights/tree')
      // console.log(res)
      if (res.meta.status !== 200) return this.$message.error('')
      this.$message.success('')
      this.rightsTree = res.data
      this.getLeafNode(role, this.defaultKey)
      this.setRightsVisible = true
    },
    //   children 
    getLeafNode(node, arr) {
      //  
      if (!node.children) {
        return arr.push(node.id)
      } else {
        // 
        for (var i = 0; i < node.children.length; i++) {
          this.getLeafNode(node.children[i], arr)
        }
      }
    },
    clearDefaultKey() {
      this.defaultKey = []
    },
    // roles
    async allocateRoles() {
      // let keys=[]
      // ref
      let keys = [...this.$refs.treeRef.getCheckedKeys(), ...this.$refs.treeRef.getHalfCheckedKeys()]
      keys = keys.join(',')
      // roles/:roleId/rights
      const { data: res } = await this.$http.post(`roles/${this.roleId}/rights`, { rids: keys })
      console.log(res)
      if (res.meta.status !== 200) return this.$message.error('')
      this.$message.success('')
      this.setRightsVisible = false
      this.getRoleList()
    }
  }
}
</script>
<style lang="less" scoped>
.tagMargin {
  margin: 20px 0;
}
.bm {
  border-bottom: 1px solid #eee;
}
.bt {
  border-top: 1px solid #eee;
}
.tMargin {
  margin-right: 5px;
}
.vcneter {
  display: flex;
  align-items: center;
}
</style>
