<template>
  <div class="container_user">
    <el-breadcrumb separator-class="el-icon-arrow-right" class="header">
      <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-card>
      <el-row :gutter="20">
        <el-col :span="8">
          <el-input placeholder="" v-model="queryInfo.query" clearable @clear="getUserList()">
            <el-button slot="append" icon="el-icon-search" @click="getUserList()"></el-button>
          </el-input>
        </el-col>
        <el-col :span="4">
          <el-button type="primary" @click="addDialogVisible = true"></el-button>
        </el-col>
      </el-row>

      <!-- -->
      <el-table :data="userList" border>
        <el-table-column type="index" label="#"> </el-table-column>
        <el-table-column prop="username" label=""> </el-table-column>
        <el-table-column prop="email" label=""> </el-table-column
        ><el-table-column prop="mobile" label=""> </el-table-column>
        <el-table-column prop="role_name" label=""> </el-table-column>
        <el-table-column prop="mg_state" label="">
          <template slot-scope="scope">
            <!--  switch -->
            <!-- {{ scope.row }} -->
            <el-switch v-model="scope.row.mg_state" @change="changeUserState(scope.row)"> </el-switch>
          </template>
        </el-table-column>
        <el-table-column label="" width="190px">
          <!--    -->
          <template slot-scope="scope">
            <!--  -->
            <el-tooltip effect="dark" content="" placement="top" :enterable="false">
              <el-button type="primary" icon="el-icon-edit" size="mini" @click="toEditUser(scope.row.id)"></el-button>
            </el-tooltip>
            <!--  -->
            <el-tooltip effect="dark" content="" placement="top" :enterable="false">
              <el-button type="danger" icon="el-icon-delete" size="mini" @click="deleteUser(scope.row.id)"></el-button>
            </el-tooltip>
            <!--  -->
            <el-tooltip effect="dark" content="" placement="top" :enterable="false">
              <el-button
                type="warning"
                v-model="scope.row.id"
                icon="el-icon-setting"
                size="mini"
                @click="alloRole(scope.row)"
              ></el-button>
            </el-tooltip>
          </template>
        </el-table-column>
      </el-table>
      <!--  -->
      <template>
        <div class="block">
          <el-pagination
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page="queryInfo.pagenum"
            :page-sizes="[1, 2, 5, 10]"
            :page-size="queryInfo.pagesize"
            layout="total, sizes, prev, pager, next, jumper"
            :total="total"
          >
          </el-pagination>
        </div>
      </template>
    </el-card>
    <!--  -->
    <el-dialog title="" :visible.sync="addDialogVisible" width="30%" @close="clearForm('addFormRef')">
      <span></span>
      <el-form :model="addUserForm" :rules="addFormsRules" ref="addFormRef" label-width="70px" class="demo-ruleForm">
        <el-form-item label="" prop="username">
          <el-input v-model="addUserForm.username"></el-input>
        </el-form-item>
        <el-form-item label="" prop="password">
          <el-input type="password" v-model="addUserForm.password"></el-input>
        </el-form-item>
        <el-form-item label="" prop="email">
          <el-input type="email" v-model="addUserForm.email"></el-input>
        </el-form-item>
        <el-form-item label="" prop="mobile">
          <el-input v-model="addUserForm.mobile"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="addDialogVisible = false"> </el-button>
        <el-button type="primary" @click="addUser"> </el-button>
      </span>
    </el-dialog>

    <!--  -->
    <el-dialog title="" :visible.sync="editDialogVisible" width="30%" @close="clearForm('editFormRef')">
      <span></span>
      <el-form :model="editForm" :rules="editFormRules" ref="editFormRef" label-width="70px" class="demo-ruleForm">
        <el-form-item label="" prop="username">
          <el-input v-model="editForm.username" disabled></el-input>
        </el-form-item>
        <el-form-item label="" prop="email">
          <el-input type="email" v-model="editForm.email"></el-input>
        </el-form-item>
        <el-form-item label="" prop="mobile">
          <el-input v-model="editForm.mobile"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editDialogVisible = false"> </el-button>
        <el-button type="primary" @click="editUser(editForm.id)"> </el-button>
      </span>
    </el-dialog>

    <!--  -->
    <el-dialog title="" :visible.sync="alloRoleDialogVisible" width="30%" @close="clearAlloRole">
      <div>
        <p>{{ userInfo.username }}</p>
        <p>{{ userInfo.role_name }}</p>
        <p>
          
          <el-select v-model="roleSelectedId" placeholder="">
            <el-option v-for="item in roleList" :key="item.id" :label="item.roleName" :value="item.id"> </el-option>
          </el-select>
        </p>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="alloRoleDialogVisible = false"> </el-button>
        <el-button type="primary" @click="editAlloRole"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
export default {
  data() {
    // 
    const emailValide = (rule, value, callback) => {
      //  
      const regEmail = /^[a-zA-Z0-9_-]+@[a-zA-Z0-9_-]+(\.[a-zA-Z0-9_-])+/
      if (regEmail.test(value)) {
        return callback()
      }
      callback(new Error(''))
    }
    // 
    const mobileValide = (rule, value, callback) => {
      //  
      const regMobile = /^(0|86|17951)?(13[0-9]|15[0123456789]17[678]|18[0-9]|14[57])[0-9]{8}$/
      if (regMobile.test(value)) {
        return callback()
      }
      callback(new Error(''))
    }
    return {
      //   
      queryInfo: {
        //
        query: '',
        // 
        pagenum: 1,
        // 
        pagesize: 2
      },
      userList: [],
      total: 0,
      addDialogVisible: false,
      addUserForm: {
        username: '',
        password: '',
        email: '',
        mobile: ''
      },
      //   prop
      addFormsRules: {
        username: [
          { required: true, message: '', trigger: 'blur' },
          { min: 3, max: 6, message: '3-6', trigger: 'blur' }
        ],
        password: [
          { required: true, message: '', trigger: 'blur' },
          { min: 6, max: 15, message: '6-15', trigger: 'blur' }
        ],
        email: [
          { required: true, message: '', trigger: 'blur' },
          { validator: emailValide, trigger: 'blur' }
        ],
        mobile: [
          { required: true, message: '', trigger: 'blur' },
          { validator: mobileValide, trigger: 'blur' }
        ]
      },
      // 
      editDialogVisible: false,
      editForm: {},
      // 
      editFormRules: {
        email: [
          { required: true, trigger: 'blur', message: '' },
          { validator: emailValide, trigger: 'blur' }
        ],
        mobile: [
          { required: true, trigger: 'blur', message: '' },
          { validator: mobileValide, trigger: 'blur' }
        ]
      },
      // 
      alloRoleDialogVisible: false,
      //  
      userInfo: [],
      roleList: [],
      roleSelectedId: ''
    }
  },
  //  
  created() {
    this.getUserList()
  },
  methods: {
    async getUserList() {
      //  axios 
      const { data: res } = await this.$http.get('/users', { params: this.queryInfo })
      // console.log(res)
      if (res.meta.status !== 200) return this.$message.error('')
      // console.log(res)
      this.userList = res.data.users
      this.total = res.data.total
      // console.log(this.userList)
      // console.log(this.total)
    },
    // pagesize 
    handleSizeChange(newsize) {
      // 
      this.queryInfo.pagesize = newsize
      //  list
      this.getUserList()
    },
    // 
    handleCurrentChange(newPage) {
      this.queryInfo.pagenum = newPage
      this.getUserList()
    },
    async changeUserState(userMsg) {
      console.log(userMsg)
      const { data } = await this.$http.put(`users/${userMsg.id}/state/${userMsg.mg_state}`)
      console.log(data)
      if (data.meta.status !== 200) {
        //  
        userMsg.mg_state = !userMsg.mg_state
        return this.$message.error('')
      }
      // 
      this.$message.success('')
    },
    // 
    clearForm(refName) {
      this.$refs[refName].resetFields()
    },
    // 
    addUser() {
      // 
      // 
      this.$refs.addFormRef.validate(async (valid) => {
        console.log(valid)
        if (!valid) return this.$message.error('')
        // ajax
        const { data: res } = await this.$http.post('users', this.addUserForm)
        // console.log(res)
        if (res.meta.status !== 201) return this.$message.error('')
        this.addDialogVisible = false
        this.$message.success('')
        this.getUserList()
      })
    },
    // 
    async deleteUser(id) {
      //  
      // alert(id)
      const { data: res } = await this.$http.delete(`users/${id}`, { id: id })
      console.log(res)
      if (res.meta.status !== 200) return this.$message.error('')
      this.$message.success('')
      this.getUserList()
    },

    // id
    async toEditUser(id) {
      const { data: res } = await this.$http.get(`users/${id}`)
      if (res.meta.status !== 200) return this.$message.error('')
      this.editForm = res.data
      this.editDialogVisible = true
    },
    async editUser(id) {
      // 
      const { data: res } = await this.$http.put(`users/${id}`, this.editForm)
      if (res.meta.status !== 200) return this.$message.error('')
      this.editDialogVisible = false
      this.$message.success('')
      this.getUserList()
    },
    // 
    async alloRole(user) {
      this.userInfo = user
      this.alloRoleDialogVisible = true
      // 
      const { data: res } = await this.$http.get('roles')
      this.roleList = res.data
    },
    // 
    async editAlloRole() {
      // console.log(this.roleSelectedId)
      if (!this.roleSelectedId) {
        return this.$message.error('')
      }
      const { data: res } = await this.$http.put(`users/${this.userInfo.id}/role`, { rid: this.roleSelectedId })
      console.log(res)
      if (res.meta.status !== 200) return this.$message.error('')
      this.alloRoleDialogVisible = false
      this.getUserList()
    },
    clearAlloRole() {
      this.roleSelectedId = ''
    }
  }
}
</script>
<style lang="less" scoped>
.header {
  font-size: 12px;
}

.el-table {
  margin-top: 10px;
}
.block {
  margin-top: 10px;
}
</style>
