<template>
  <div class="container_login">
    <div class="login_box">
      <!--  -->
      <div class="avatar_box">
        <img src="../assets/logo.png" alt="" />
      </div>
      <!--  ref -->
      <div class="formBox">
        <el-form
          :model="loginform"
          :rules="loginFormRules"
          label-width="0px"
          ref="loginFormRef"
        >
          <!--  -->
          <el-form-item prop="username">
            <el-input
              prefix-icon="el-icon-user"
              v-model="loginform.username"
            ></el-input>
          </el-form-item>
          <!--  -->
          <el-form-item prop="password">
            <el-input
              prefix-icon="el-icon-s-goods"
              v-model="loginform.password"
              type="password"
            ></el-input>
          </el-form-item>
          <!--  -->
          <el-form-item class="btnDev">
            <el-button type="primary" @click="login"></el-button>
            <el-button type="info" @click="resetLoginForm"></el-button>
          </el-form-item>
        </el-form>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      // 
      loginform: {
        username: 'admin',
        password: '123456'
      },
      // 
      loginFormRules: {
        // 
        username: [
          { required: true, message: '', trigger: 'blur' },
          { min: 3, max: 8, message: '3-8', trigger: 'blur' }
        ],
        password: [
          { required: true, message: '', trigger: 'blur' },
          { min: 6, max: 15, message: '6-10', trigger: 'blur' }
        ]
      }
    }
  },
  methods: {
    // 
    resetLoginForm() {
      // this Vue
      console.log(this)
      this.$refs.loginFormRef.resetFields()
    },
    login() {
      //
      this.$refs.loginFormRef.validate(async (valid) => {
        // true 
        console.log(valid)
        if (valid) {
          // ajax 
          const { data: res } = await this.$http.post('/login', this.loginform)
          // console.log(res)
          // 
          if (res.meta.status === 200) {
            this.$message.success('')
            // session token
            window.sessionStorage.setItem('token', res.data.token)
            //  
            this.$router.push('/home')
          } else {
            // 
            this.$message.error('')
          }
        }
      })
    }
  }
}
</script>
