<template>
  <div class="report_container">
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-card>
      <div id="echarts_container" style="width: 850px;height:500px;"></div>
    </el-card>
  </div>
</template>
<script>
// echarts
import echarts from 'echarts'
export default {
  data() {
    return {
      // 
      options: {
        title: {
          text: ''
        },
        tooltip: {
          trigger: 'axis',
          axisPointer: {
            type: 'cross',
            label: {
              backgroundColor: '#E9EEF3'
            }
          }
        },
        grid: {
          left: '3%',
          right: '4%',
          bottom: '3%',
          containLabel: true
        },
        xAxis: [
          {
            boundaryGap: false
          }
        ],
        yAxis: [
          {
            type: 'value'
          }
        ]
      }
    }
  },
  created() {},
  methods: {},
  //    mounted
  async mounted() {
    var myChart = echarts.init(document.getElementById('echarts_container'))
    const { data: res } = await this.$http.get('reports/type/1')
    // 
    if (res.meta.status !== 200) this.$message.error('')
    console.log(res.data)
    const newOption = { ...this.options, ...res.data }
    console.log(newOption)
    // 
    myChart.setOption(newOption)
  }
}
</script>
